.team-member--container {
	background-color: rgb(255, 255, 255);
	padding: 20px 20px;
	box-shadow: 1px 2px 8px 0 #c6c5c5;

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}

	.bio--textbox {
		margin-bottom: 0;
	}
}
